<template>
    <main class="main">
        <div class="main__search">
            <input 
            type="text" 
            placeholder="Введите имя пользователя" 
            class="main__search-input"
            v-model="getUsername"
            >
        <button class="main__search-btn" @click="getData(); getRepos()">НАЙТИ</button>
        </div>
        
        <div class="main__info">
            <div class="main__info-left">
                <div class="main__info-left-img">
                    <img
                    v-if="data"
                    :src="data.avatar_url"
                    class="avatar"
                    alt=""
                    >
                </div>
                <a 
                v-if="data"
                :href="`https://github.com/${data.login}`" 
                class="main__info-left-link"
                target="_blank"
                >ПОСЕТИТЬ</a>
            </div>
            <div class="main__info-right" v-if="data">
                <p class="main__info-right-text">{{ data.login }}</p>
                <p class="main__info-right-text">Репозиториев: {{ data.public_repos }}</p>
                <p class="main__info-right-text">Создан: {{ data.created_at.slice(0, 10) }}</p>
                <p class="main__info-right-text">Подписщиков: {{ data.followers }}</p>
                <p class="main__info-right-text">Подписок: {{ data.following }}</p>
            </div>
        </div>

        <div class="main__sort">
            <h2 class="main__sort-title">Сортировка</h2>
            <div class="main__sort-type">
                <h3 class="main__sort-type-name" 
                @click="sortByName()"
                >ИМЯ</h3>
                <h3 class="main__sort-type-stars"
                @click="sortByStars()"
                >ЗВЕЗДЫ</h3>
                <h3 class="main__sort-type-date"
                @click="sortByDate()"
                >ДАТА</h3>
            </div>
            <div class="main__sort-repos" v-if="repos">
                <div class="main__sort-repos-item" v-for="rep in repos" :key="rep.id">
                    <div class="main__sort-repos-item-txbox">
                        <h3 class="main__sort-repos-item-title">{{ rep.name }}</h3>
                        <p class="main__sort-repos-item-text">Кол-во звёзд: {{ rep.stargazers_count }}</p>
                        <p class="main__sort-repos-item-text">Дата добавления: {{ rep.created_at.slice(0, 10) }}</p>
                    </div>
                    <a :href="`https://github.com/${rep.full_name}`" class="main__info-left-link" target="_blank">ПОСЕТИТЬ</a>
                </div>
            </div>
                <div class="loading-spinner" v-else>
                <div class="loading-spinner-inner">
                <div class="loading-spinner-circle"></div>
                <div class="loading-spinner-circle"></div>
                <div class="loading-spinner-circle"></div>
                <div class="loading-spinner-circle"></div>
                <div class="loading-spinner-circle"></div>
                </div>
                </div>
        </div>
    </main>
</template>

<script>
import { mapState, mapActions, mapMutations } from 'vuex';
    export default {
        data() {
            return {
            }
        },

        methods: {
            ...mapMutations(['setUsername']),
            ...mapActions(['getData','getRepos','sortByDate','sortByStars','sortByName']),
            
        },
        computed: {
            ...mapState(['username','data','repos']),
            getUsername: {
                get() {
                    return this.username
                },
                set(val) {
                    this.setUsername(val)
                }
            }
        }
    }
</script>
