import { createStore } from 'vuex'
import axios from 'axios'

export default createStore({
  state: {
    username: '',
    data: null,
    repos: null
  },
  getters: {
  },
  mutations: {
    setUsername(state, payload) {
      state.username = payload
    },
    getData(state, payload) {
      state.data = payload
    },
    getRepos(state, payload) {
      state.repos = payload
    },
    sortByName(state, payload) {
      state.repos = payload
    },
    sortByStars(state, payload) {
      state.repos = payload
    },
    sortByDate(state, payload) {
      state.repos = payload
    }
  },
  actions: {
    async getData(context) {
      try {
        let user = context.state.username
        let response = await axios.get(`https://api.github.com/users/${user}`)
        console.log(response.data);
        context.commit('getData', response.data)
      } catch (error) {
        console.error('Ошибка при получении данных ' + error)
      }
    },
    async getRepos(context) {
      try {
        let user = context.state.username
        let response = await axios.get(`https://api.github.com/users/${user}/repos`)
        context.commit('getRepos', response.data)
      } catch (error) {
        console.error('Ошибка при получении данных ' + error)
      }
    },
    sortByName(context) {
      if (Array.isArray(context.state.repos)) {
        const sortedData = context.state.repos.sort((a,b) => a.name.localeCompare(b.name))
        context.commit('sortByName', sortedData);
      } else {
        context.commit('sortByName', []);
      }
    },
    sortByStars(context) {
      if (Array.isArray(context.state.repos)) {
        const sortedData = context.state.repos.sort((a,b) => b.stargazers_count - a.stargazers_count)
        context.commit('sortByStars', sortedData);
      } else {
        context.commit('sortByStars', []);
      }
    },
    sortByDate(context) {
      if (Array.isArray(context.state.repos)) {
        const sortedData = context.state.repos.sort((a, b) => new Date(b.created_at) - new Date(a.created_at));
        context.commit('sortByDate', sortedData);
      } else {
        context.commit('sortByDate', []);
      }
    }
  },
  modules: {
  }
})
